import React, { useEffect, useState } from 'react'
import { Row, Col, Card, Button, Table } from 'react-bootstrap';
import Swal from 'sweetalert2';
import { useSelector } from 'react-redux';
import withReactContent from 'sweetalert2-react-content';
import Aux from '../../../hoc/_Aux';
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import { NoAutorizado } from '../base/NoAutorizado';
import Loading from '../base/Loading';
import { asignarEstiloTabla, limpiarEstiloTabla } from '../../../helpers/estiloTabla';
import { ProductoUpSert } from './ProductoUpSert';
import { StockProductoLista } from './StockProductoLista';
import { v4 as uuidv4 } from 'uuid';
const menuId = 36;
const menuIdCategoria = 32;
const menuIdTipoProducto = 35;
const menuIdStock = 38;
const menuIdTipoPresentacion = 38;//48;
export const ProductoListar = () => {
    const state = useSelector(state => state);
    const [loading, setLoading] = useState(true);
    const [idAuxRender,setIdAuxRender]=useState(uuidv4());
    const [accesos, setAccesos] = useState([]);
    const [abrirModal, setAbrirModal] = useState(false);
    const [listCategoria, setListCategoria] = useState([]);
    const [litTipoPresentacion, setLitTipoPresentacion] = useState([]);
    const [listTipoProducto, setListTipoProducto] = useState([]);
    const [verStock, setVerStock] = useState(false);

    const [listProductos, setListProductos] = useState([]);
    const initData = {
        productoId: '',
        categoriaId: '',
        tipo_productoId: '',
        codigo: '',
        nombre: '',
        descripcion: '',
        puede_ser_vendido: true,
        puede_ser_comprado: true,
        perecedero: false,
        estadoId: 1
    };


    const GetAccesosByMenuId = () => {
        if (state?.accesos) {
            const { accesos } = state;
            const misAccesos = accesos.filter(item => item.menuId === menuId || item.menuId === menuIdCategoria || item.menuId === menuIdTipoProducto || item.menuId === menuIdStock);
            setAccesos(misAccesos);
        }
        setLoading(false);
    }

    const [dataInicial, setdataInicial] = useState(initData);
    const handleOpenModal = () => {
        setAbrirModal(true);
        setdataInicial(initData);
    }
    const GetListado = async () => {
        if (accesos.find(acceso => acceso.accesoId === 3 && acceso.menuId === menuId)) {
            limpiarEstiloTabla("#tableProductos");
            setLoading(true);
            let response = await callApi('producto?estadoId=1;2');
            if (response) {
                setListProductos(response);
            }
            asignarEstiloTabla("#tableProductos", 50);

        }
        setLoading(false);
    }

    const GetCategorias = async () => {
        if (accesos.find(acceso => acceso.accesoId === 3 && acceso.menuId === menuIdCategoria)) {
            setLoading(true);
            let response = await callApi('categoria?estadoId=1;2');
            if (response) {
                setListCategoria(response);
            }
        }else{
            setListCategoria([{categoriaid:'',nombre:'No esta autorizado'}]);
        }
        setLoading(false);
    }
    const GetTipoPresentacion = async () => {
        if (accesos.find(acceso => acceso.accesoId === 3 && acceso.menuId === menuIdTipoPresentacion)) {
            setLoading(true);
            let response = await callApi('tipopresentacion?estadoId=1;2&include=0');
            if (response) {
                setLitTipoPresentacion(response);
            }
        }else{
            setLitTipoPresentacion([{tipo_presentacionId:'',nombre:'No esta autorizado'}]);
        }
        setLoading(false);
    }
    
    const GetTipoProducto = async () => {
        if (accesos.find(acceso => acceso.accesoId === 3 && acceso.menuId === menuIdTipoProducto)) {
            setLoading(true);
            let response = await callApi('tipoproducto?estadoId=1;2');
            if (response) {
                setListTipoProducto(response);
            }
        }
        setLoading(false);
    }


    const handleEditar = (id) => {
        const { productoId, categoriaId, tipo_productoId, codigo, nombre, descripcion, precio, costo, puede_ser_vendido = true, puede_ser_comprado = true, perecedero = false, estadoId,TipoPresentacion=[] } = listProductos.find(item => item.productoId === id);
        let nuevos=TipoPresentacion.map(itemTipo=>{
            itemTipo.editar=false;
            return itemTipo;
        });

        setdataInicial({
            productoId,
            categoriaId,
            tipo_productoId,
            codigo,
            nombre,
            descripcion,
            precio,
            costo,
            puede_ser_vendido: puede_ser_vendido,
            puede_ser_comprado: puede_ser_comprado,
            perecedero: perecedero,
            estadoId,
            TipoPresentacion:nuevos
        });
        setAbrirModal(true);
    }
    const handleVerStock = (id) => {
        const { productoId, nombre,perecedero } = listProductos.find(item => item.productoId === id);
        setdataInicial({
            productoId,
            nombre,
            perecedero
        });
        setVerStock(true);
    }

    const handleDelete = (id) => {
        const MySwal = withReactContent(Swal);
        MySwal.fire({
            title: 'Alerta?',
            text: 'Esta seguro que desea eliminar el elemento',
            type: 'warning',
            showCloseButton: true,
            showCancelButton: true
        }).then(async (willDelete) => {
            if (willDelete.value) {
                let method = 'DELETE';
                let response = await callApi(`producto/${id}`, {
                    method
                });
                if (response) {
                    alert_exitoso(response);
                    let listActual = listProductos.filter(item => item.productoId !== id);
                    setListProductos(listActual);
                }
            } else {
                alert_warning('No se eliminó ningún elemento');
            }
        });
    }
    useEffect(() => {
        limpiarEstiloTabla("#tableProductos");
        setIdAuxRender(uuidv4());
        asignarEstiloTabla("#tableProductos", 50);
    }, [abrirModal]);
    
    useEffect(() => {
        GetAccesosByMenuId();
    }, [state?.accesos]);

    useEffect(() => {
        GetListado();
        GetCategorias();
        GetTipoProducto();
        GetTipoPresentacion();
    }, [accesos]);
    return (
        accesos.length > 0 &&
        <>
            {
                !verStock ?
                    <Aux>
                        <Row className='btn-page'>
                            <Col sm={12}>
                                <Card>
                                    <Card.Header>
                                        <Card.Title as="h5">Productos</Card.Title>
                                    </Card.Header>
                                    <Card.Body>
                                        {
                                            loading === true ?
                                                <Loading />
                                                : <>
                                                    <Row className="align-items-center m-l-0">
                                                        <Col />
                                                        <Col className="text-right">
                                                            {
                                                                accesos.find(acceso => acceso.menuId===menuId && acceso.accesoId === 1) &&
                                                                <Button variant="success" className="btn-sm btn-round has-ripple" onClick={handleOpenModal}><i className="feather icon-plus" /> Agregar Producto</Button>
                                                            }
                                                        </Col>
                                                    </Row>
                                                    {
                                                        accesos.find(acceso => acceso.accesoId === 3 && acceso.menuId === menuId) ?
                                                            <Table striped hover responsive bordered id="tableProductos">
                                                                <thead>
                                                                    <tr>
                                                                        <th>Codigo</th>
                                                                        {
                                                                            accesos.find(acceso => acceso.accesoId === 3 && acceso.menuId === menuIdStock) &&
                                                                            <th style={{ textAlign: "center" }}>Stock</th>
                                                                        }
                                                                        <th>Codigo Barra</th>
                                                                        <th>Categoria</th>
                                                                        <th>Tipo</th>
                                                                        <th>Nombre</th>
                                                                        <th>Perecedero</th>
                                                                        <th>Estado</th>
                                                                        {
                                                                            accesos.find(acceso => (acceso.accesoId === 2 && acceso.menuId === menuId) || (acceso.accesoId === 4 && acceso.menuId === menuId)) &&
                                                                            <th></th>
                                                                        }
                                                                    </tr>
                                                                </thead>
                                                                <tbody>
                                                                    {
                                                                        listProductos.map(({ productoId, codigo, precio, costo, puede_ser_vendido, puede_ser_comprado, perecedero, Categoria: { nombre: nombreCategoria }, TipoProducto: { descripcion: descTipo }, nombre, descripcion, Estado: { descripcion: estado } }) => {
                                                                            return (
                                                                                <tr key={productoId}>
                                                                                    <td>{productoId}</td>
                                                                                    {
                                                                                        accesos.find(acceso => acceso.accesoId === 3 && acceso.menuId === menuIdStock) &&
                                                                                        <td style={{ textAlign: "center" }}>
                                                                                            <button className="btn-icon btn btn-info btn-sm" onClick={() => { handleVerStock(productoId) }}><i className="feather icon-list" /></button>
                                                                                        </td>
                                                                                    }
                                                                                    <td>{codigo}</td>
                                                                                    <td>{nombreCategoria}</td>
                                                                                    <td>{descTipo}</td>
                                                                                    <td>{nombre}</td>
                                                                                    <td style={{ textAlign: "center" }}>{perecedero === true ? 'SI' : 'NO'}</td>
                                                                                    <td>{estado}</td>

                                                                                    {
                                                                                        accesos.find(acceso => (acceso.accesoId === 2 && acceso.menuId === menuId) || (acceso.accesoId === 4 && acceso.menuId === menuId)) &&
                                                                                        <td style={{ textAlign: "center" }}>
                                                                                            {
                                                                                                accesos.find(acceso => acceso.accesoId === 2 && acceso.menuId === menuId) &&
                                                                                                <button className="btn-icon btn btn-info btn-sm" onClick={() => { handleEditar(productoId) }}><i className="feather icon-edit" /></button>
                                                                                            }
                                                                                            {
                                                                                                accesos.find(acceso => acceso.accesoId === 4 && acceso.menuId === menuId) &&
                                                                                                <button className="btn-icon btn btn-danger btn-sm" onClick={() => { handleDelete(productoId) }}><i className="feather icon-trash-2" /></button>
                                                                                            }
                                                                                        </td>
                                                                                    }
                                                                                </tr>
                                                                            )
                                                                        })
                                                                    }
                                                                </tbody>
                                                            </Table>
                                                            : <NoAutorizado />
                                                    }
                                                </>
                                        }
                                        {
                                            abrirModal === true &&
                                            <ProductoUpSert
                                                idAuxRender={idAuxRender}
                                                abrirModal={abrirModal}
                                                setAbrirModal={setAbrirModal}
                                                GetListado={GetListado}
                                                dataInicial={dataInicial}
                                                listCategoria={listCategoria}
                                                listTipoProducto={listTipoProducto}
                                                listTipoPresentacion={litTipoPresentacion}
                                            />
                                        }
                                    </Card.Body>
                                </Card>
                            </Col>
                        </Row>
                    </Aux>
                    : <StockProductoLista
                        setVerStock={setVerStock}
                        {...dataInicial}
                    />
            }
        </>
    );
}
