import React, { useEffect, useState } from 'react'
import moment from 'moment';
import { Table, Form, Modal } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso } from '../../../helpers/Notificacion';
import { NoAutorizado } from '../base/NoAutorizado';
import Loading from '../base/Loading';
const menuId = 39;
export const LoteProductoListar = ({ productoId, nombre: nombreProducto, abrirModal, setAbrirModal }) => {
    const state = useSelector(state => state);
    const [loading, setLoading] = useState(true)
    const dispatch = useDispatch();
    const [accesos, setAccesos] = useState([]);
    const [listLoteProducto, setListLoteProducto] = useState([]);

    const GetAccesosByMenuId = () => {
        if (state?.accesos) {
            const { accesos } = state;
            const misAccesos = accesos.filter(item => item.menuId === menuId);
            setAccesos(misAccesos);
        }
        setLoading(false);
    }



    const GetLoteProducto = async () => {
        if (accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 3)) {
            setLoading(true);
            let response = await callApi(`loteproducto?productoId=${productoId}`);
            if (response) {
                setListLoteProducto(response);
            }
            setLoading(false);
        }
    }

    useEffect(() => {
        GetAccesosByMenuId();
    }, []);

    useEffect(() => {
        GetLoteProducto();
    }, [accesos]);

    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(false)} size="xl">
            {
                loading === true ?
                    <Loading />
                    : <>
                        <Modal.Header closeButton>
                            <Modal.Title as="h5">Lotes</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            {
                                accesos.find(acceso => acceso.accesoId === 3) ?
                                    <Table striped hover responsive bordered id="tableStockProducto">
                                        <thead>
                                            <tr>
                                                <th>Codigo</th>
                                                <th>Producto</th>
                                                <th>Sucursal</th>
                                                <th style={{ textAlign: "center" }}>Lote</th>
                                                <th style={{ textAlign: "center" }}>Fecha de Vencimiento</th>
                                                <th style={{ textAlign: "center" }}>Comprado</th>
                                                <th style={{ textAlign: "center" }}>Disponible</th>
                                                <th>Fecha Creación</th>
                                                <th>Estado</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                listLoteProducto.map(({ lote_productoId, Sucursal: { nombre: desSucursal }, descripcion, cantidad_inicial, disponible, fecha_crea,fecha_vencimiento, Estado: { descripcion: estado } }) => (
                                                    <tr key={lote_productoId}>
                                                        <td>{lote_productoId}</td>
                                                        <td>{nombreProducto}</td>
                                                        <td>{desSucursal}</td>
                                                        <td style={{ textAlign: "center" }}>{descripcion}</td>
                                                        <td style={{ textAlign: "center" }}>{moment(fecha_vencimiento).format('DD/MM/YYYY')}</td>
                                                        <td style={{ textAlign: "center" }}>{cantidad_inicial}</td>
                                                        <td style={{ textAlign: "center" }}>{disponible}</td>
                                                        <td>{moment(fecha_crea).format('DD/MM/YYYY')}</td>
                                                        <td>{estado}</td>
                                                    </tr>
                                                ))
                                            }
                                        </tbody>
                                    </Table>
                                    : <NoAutorizado />
                            }
                        </Modal.Body>
                    </>
            }
        </Modal>
    )
}
