import React, { useEffect, useState } from 'react'
import moment from 'moment';
import { Row, Col, Card, Button, Table } from 'react-bootstrap';
import Swal from 'sweetalert2';
import { useSelector } from 'react-redux';
import withReactContent from 'sweetalert2-react-content';
import Aux from '../../../hoc/_Aux';
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import { CategoriaProductoUpSert } from './CategoriaProductoUpSert';
import { NoAutorizado } from '../base/NoAutorizado';
import Loading from '../base/Loading';
import { asignarEstiloTabla, limpiarEstiloTabla } from '../../../helpers/estiloTabla';
import { StockProductoUpSert } from './StockProductoUpSert';
import { LoteProductoListar } from './LoteProductoListar';
const menuId = 38;
const menuIdSucursal = 33;
const menuIdLoteProducto=39;
export const StockProductoLista = ({ nombre, productoId,perecedero, setVerStock }) => {
    const state = useSelector(state => state);
    const [loading, setLoading] = useState(true)
    const [accesos, setAccesos] = useState([]);
    const [abrirModal, setAbrirModal] = useState(false);
    const [abrirModalLoteProducto, setAbrirModalLoteProducto] = useState(false);
    const [listSucursales, setListSucursales] = useState([]);

    const [listStockProducto, setListStockProducto] = useState([]);
    const initData = {
        stockId: '',
        productoId: productoId,
        sucursalId: '',
        minimo: 0,
        maximo: 0,
        disponible: 0,
        estadoId: 1
    };

    const GetAccesosByMenuId = () => {
        if (state?.accesos) {
            const { accesos } = state;
            const misAccesos = accesos.filter(item => item.menuId === menuId || item.menuId === menuIdSucursal || item.menuId===menuIdLoteProducto);
            setAccesos(misAccesos);
        }
        setLoading(false);
    }

    const [dataInicial, setdataInicial] = useState(initData);
    const handleOpenModal = () => {
        setAbrirModal(true);
        setdataInicial(initData);
    }
    const GetListado = async () => {
        if (accesos.find(acceso => acceso.accesoId === 3 && acceso.menuId === menuId)) {
            limpiarEstiloTabla("#tableStockProducto");
            setLoading(true);
            let response = await callApi(`stock?estadoId=1;2&productoId=${productoId}`);
            if (response) {
                setListStockProducto(response);
            }
            asignarEstiloTabla("#tableStockProducto");

        }
        setLoading(false);
    }

    const GetSucursales = async () => {
        if (accesos.find(acceso => acceso.accesoId === 3 && acceso.menuId === menuIdSucursal)) {
            setLoading(true);
            let response = await callApi(`sucursal?estadoId=1`);
            if (response) {
                setListSucursales(response);
            }
        }
        setLoading(false);
    }
    const handleEditar = (id) => {
        const { stockId, sucursalId, minimo, maximo, disponible, estadoId } = listStockProducto.find(item => item.stockId === id);
        setdataInicial({
            stockId,
            sucursalId,
            minimo,
            maximo,
            disponible,
            estadoId
        });
        setAbrirModal(true);
    }
    const handleDelete = (id) => {
        const MySwal = withReactContent(Swal);
        MySwal.fire({
            title: 'Alerta?',
            text: 'Esta seguro que desea eliminar el elemento',
            type: 'warning',
            showCloseButton: true,
            showCancelButton: true
        }).then(async (willDelete) => {
            if (willDelete.value) {
                let method = 'DELETE';
                let response = await callApi(`stock/${id}`, {
                    method
                });
                if (response) {
                    alert_exitoso(response);
                    let listActual = listStockProducto.filter(item => item.stockId !== id);
                    setListStockProducto(listActual);
                }
            } else {
                alert_warning('No se eliminó ningún elemento');
            }
        });
    }
    useEffect(() => {
        GetAccesosByMenuId();
    }, [state?.accesos]);

    useEffect(() => {
        GetListado();
        GetSucursales();
    }, [accesos]);
    return (
        accesos.length > 0 &&
        <Aux>
            <Row className='btn-page'>
                <Col sm={12}>
                    <Card>
                        <Card.Header>
                            <Card.Title as="h5"><button className="btn btn-success btn-sm" type="button" onClick={() => setVerStock(false)}>Regresar</button>STOCK DEL PRODUCTO {nombre}</Card.Title>
                        </Card.Header>
                        <Card.Body>
                            {
                                loading === true ?
                                    <Loading />
                                    : <>
                                    {
                                         accesos.find(acceso => acceso.accesoId === 3 && acceso.menuId===menuIdLoteProducto) &&
                                        <Row className="align-items-center m-l-0">
                                            <Col className="text-left">
                                                {
                                                    (accesos.find(acceso => acceso.accesoId === 1 && acceso.menuId === menuId) && perecedero===true)&&
                                                    <Button variant="info" className="btn-sm btn-round has-ripple" onClick={()=>setAbrirModalLoteProducto(true)}><i className="feather icon-eye" /> Ver Lotes</Button>
                                                }
                                            </Col>
                                            <Col />
                                        </Row>
}
                                        <Row className="align-items-center m-l-0">
                                            <Col />
                                            <Col className="text-right">
                                                {
                                                    accesos.find(acceso => acceso.accesoId === 1 && acceso.menuId === menuId) &&
                                                    <Button variant="success" className="btn-sm btn-round has-ripple" onClick={handleOpenModal}><i className="feather icon-plus" /> Agregar Stock</Button>
                                                }
                                            </Col>
                                        </Row>
                                        {
                                            accesos.find(acceso => acceso.accesoId === 3 && acceso.menuId===menuId) ?
                                                <Table striped hover responsive bordered id="tableStockProducto">
                                                    <thead>
                                                        <tr>
                                                            <th>Codigo</th>
                                                            <th>Sucursal</th>
                                                            <th style={{ textAlign: "center" }}>Minimo</th>
                                                            <th style={{ textAlign: "center" }}>Maximo</th>
                                                            <th style={{ textAlign: "center" }}>Disponible</th>
                                                            <th>Fecha Asignación</th>
                                                            <th>Estado</th>
                                                            {
                                                                accesos.find(acceso => (acceso.accesoId === 2 && acceso.menuId === menuId) || (acceso.accesoId === 4 && acceso.menuId === menuId)) &&
                                                                <th></th>
                                                            }

                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {
                                                            listStockProducto.map(({ stockId, Sucursal: { nombre: desSucursal }, minimo, maximo, disponible, fecha_crea, Estado: { descripcion: estado } }) => (
                                                                <tr key={stockId}>
                                                                    <td>{stockId}</td>
                                                                    <td>{desSucursal}</td>
                                                                    <td style={{ textAlign: "center" }}>{minimo}</td>
                                                                    <td style={{ textAlign: "center" }}>{maximo}</td>
                                                                    <td style={{ textAlign: "center" }}>{disponible}</td>
                                                                    <td>{moment(fecha_crea).format('DD/MM/YYYY')}</td>
                                                                    <td>{estado}</td>
                                                                    {
                                                                        accesos.find(acceso => (acceso.accesoId === 2 && acceso.menuId === menuId) || (acceso.accesoId === 4 && acceso.menuId === menuId)) &&
                                                                        <td style={{ textAlign: "center" }}>
                                                                            {
                                                                                accesos.find(acceso => acceso.accesoId === 2 && acceso.menuId === menuId) &&
                                                                                <button className="btn-icon btn btn-info btn-sm" onClick={() => { handleEditar(stockId) }}><i className="feather icon-edit" /></button>
                                                                            }
                                                                            {
                                                                                accesos.find(acceso => acceso.accesoId === 4 && acceso.menuId === menuId) &&
                                                                                <button className="btn-icon btn btn-danger btn-sm" onClick={() => { handleDelete(stockId) }}><i className="feather icon-trash-2" /></button>
                                                                            }
                                                                        </td>
                                                                    }
                                                                </tr>
                                                            ))
                                                        }
                                                    </tbody>
                                                </Table>
                                                : <NoAutorizado />
                                        }
                                    </>
                            }
                            {
                                abrirModal === true &&
                                <StockProductoUpSert
                                    abrirModal={abrirModal}
                                    setAbrirModal={setAbrirModal}
                                    GetListado={GetListado}
                                    dataInicial={dataInicial}
                                    listSucursales={listSucursales} />
                            }

                            {
                                abrirModalLoteProducto === true &&
                                <LoteProductoListar
                                    abrirModal={abrirModalLoteProducto}
                                    setAbrirModal={setAbrirModalLoteProducto}
                                    nombre={nombre}
                                    productoId={productoId}
                                />
                            }
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux>
    );
}
