import React, { useState, useEffect } from 'react'
import { Col, Form, Modal, Row } from 'react-bootstrap';
import { ValidationForm, TextInput, SelectGroup } from 'react-bootstrap4-form-validation';
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import { useForm } from '../../hooks/useForm';
import Loading from '../base/Loading';

import { Table } from 'react-bootstrap';
import { FilaTipoPresentacion } from './FilaTipoPresentacion';
export const ProductoUpSert = ({ idAuxRender,dataInicial, abrirModal, setAbrirModal, GetListado, listCategoria = [], listTipoProducto = [], listTipoPresentacion = [] }) => {
    const [values, handleOnChange, , setValues] = useForm(dataInicial);
    const [loading, setLoading] = useState(false);
    const [addNuevo, setAddNuevo] = useState(false);
    const [editarItem, setEditarItem] = useState(false);
    const [listTipoPresentacionAsignados, setListTipoPresentacionAsignados] = useState([]);
    const [puedeSerComprado, setPuedeSerComprado] = useState(dataInicial.puede_ser_comprado)
    const [puedeSerVendido, setPuedeserVendido] = useState(dataInicial.puede_ser_vendido)
    const [esPerecedero, setEsPerecedero] = useState(dataInicial.perecedero);
    const [editCode, setEditCode] = useState(false);
    const NuevoRegistro = async () => {
        if (validarDefaultComprayVenta()) {
            let jsonRequest = { ...values, tipoPresentacion: listTipoPresentacionAsignados }
            let response = await callApi('producto', {
                method: 'POST',
                body: JSON.stringify(jsonRequest)
            });

            if (response) {
                alert_exitoso("Producto registrado exitosamente");
                GetListado();
                setAbrirModal(false);

            }
        }
    }
    const ActualizarRegistro = async () => {
        let jsonRequest = { ...values, tipoPresentacion: listTipoPresentacionAsignados }
        let response = await callApi(`producto/${dataInicial.productoId}`, {
            method: 'PUT',
            body: JSON.stringify(jsonRequest)
        });

        if (response) {
            alert_exitoso(response);
            GetListado();
        }
        setAbrirModal(false);
    }
    const handleOnSubmit = async (e) => {
        e.preventDefault();

        setLoading(true);
        if (dataInicial.productoId > 0) {
            await ActualizarRegistro();
        } else {
            await NuevoRegistro();
        }
        setLoading(false);

    }

    const handleVedible = () => {
        setPuedeserVendido(!puedeSerVendido);
        setValues({
            ...values,
            puede_ser_vendido: !puedeSerVendido
        });
    }
    const handleComprable = () => {
        setPuedeSerComprado(!puedeSerComprado);
        setValues({
            ...values,
            puede_ser_comprado: !puedeSerComprado
        });
    }

    const handlePerecedero = () => {
        setEsPerecedero(!esPerecedero);
        setValues({
            ...values,
            perecedero: !esPerecedero
        });

    }

    const handleErrorSubmit = (e, formData, errorInputs) => {
        alert_warning("Por favor complete toda la información solicitada por el formulario");
    };

    useEffect(() => {
        if (dataInicial.productoId > 0) {
            let nuevos=dataInicial.TipoPresentacion ?? [].map(item=>{
                delete item.editar;
                delete item.editDefaultCompra;
                delete item.editDefaultVenta;
                return item;
            })
            console.log({nuevos});
            setListTipoPresentacionAsignados(nuevos);
            setEditCode(true);
        }
    }, []);
    
    const validarDefaultComprayVenta = () => {
        if (listTipoPresentacionAsignados) {
            if (listTipoPresentacionAsignados.length > 0) {
                let existeDefaulCompra = listTipoPresentacionAsignados.filter(i => Boolean(i.default_compra) === true);
                if (existeDefaulCompra.length > 0) {
                    if (existeDefaulCompra.length === 1) {
                        let existeDefaulVenta = listTipoPresentacionAsignados.filter(i => Boolean(i.default_venta) === true);
                        if (existeDefaulVenta.length > 0) {
                            if (existeDefaulVenta.length === 1) {
                                return true;
                            } else {
                                alert_warning("Solo puede existir un tipo de presentación como default para la compra o venta 1");
                                return false;
                            }
                        } else {
                            alert_warning("Para poder continuar es necesario que exista un tipo de presentación como default para el registro de las ventas 2");
                            return false;
                        }
                    } else {
                        alert_warning("Solo puede existir un tipo de presentación como default para la compra o venta 3");
                        return false;
                    }
                } else {
                    alert_warning("Para poder continuar es necesario que exista un tipo de presentación como default para el registro de las compras 4");
                    return false;
                }
            } else {
                alert_warning("Se debe de agregar información sobre los tipos de presentación disponibles 5");
                return false;
            }
        } else {
            alert_warning("Se debe de agregar información sobre los tipos de presentación disponibles 6");
            return false;
        }
    }

    
    const errorMessage = "Campo obligatorio";
    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(false)} size="xl">
            {
                loading === true ?
                    <Loading />
                    : <>
                        <Modal.Header closeButton>
                            <Modal.Title as="h5">{dataInicial.productoId > 0 ? 'Actualizar Producto' : 'Nuevo Producto'}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <ValidationForm onSubmit={handleOnSubmit} onErrorSubmit={handleErrorSubmit}>
                                <h5>Información</h5>
                                <hr></hr>
                                <Form.Row>
                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="codigo">Código de Barras</Form.Label>
                                        <TextInput
                                            name="codigo"
                                            id="codigo"
                                            required
                                            value={values.codigo}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Código de barras"
                                            autoComplete="off"
                                            type="text"
                                            readOnly={editCode}
                                        />
                                    </Form.Group>
                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="nombre">Nombre</Form.Label>
                                        <TextInput
                                            name="nombre"
                                            id="nombre"
                                            required
                                            value={values.nombre}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Nombre"
                                            autoComplete="off"
                                            type="text"
                                        />
                                    </Form.Group>
                                    <Form.Group as={Col} md="12">
                                        <Form.Label htmlFor="descripcion">Descripción</Form.Label>
                                        <TextInput
                                            name="descripcion"
                                            id="descripcion"
                                            multiline
                                            value={values.descripcion}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Descripción"
                                            autoComplete="off"
                                            type="text"

                                        />
                                    </Form.Group>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="categoriaId">Categoría</Form.Label>
                                        <SelectGroup
                                            name="categoriaId"
                                            id="categoriaId"
                                            value={values.categoriaId}
                                            required
                                            errorMessage={errorMessage}
                                            onChange={handleOnChange}>
                                            <option value="">Seleccione una categoría</option>
                                            {
                                                listCategoria.map(({ categoriaId, nombre }) => (
                                                    <option key={nombre} value={categoriaId}>{nombre}</option>
                                                ))
                                            }
                                        </SelectGroup>
                                    </Form.Group>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="tipo_productoId">Tipo de Producto</Form.Label>
                                        <SelectGroup
                                            name="tipo_productoId"
                                            id="tipo_productoId"
                                            value={values.tipo_productoId}
                                            required
                                            errorMessage={errorMessage}
                                            onChange={handleOnChange}>
                                            <option value="">Seleccione un tipo de producto</option>
                                            {
                                                listTipoProducto.map(({ tipo_productoId, descripcion }) => (
                                                    <option value={tipo_productoId} key={descripcion}>{descripcion}</option>
                                                ))
                                            }
                                        </SelectGroup>
                                    </Form.Group>

                                    <Form.Group as={Col} md="4">
                                        <Form.Label htmlFor="puede_ser_comprado">Puede ser Comprado&nbsp;&nbsp;</Form.Label>
                                        <div className="switch d-inline m-r-10">
                                            <Form.Control type="checkbox" id="puede_ser_comprado" name="puede_ser_comprado" defaultChecked={puedeSerComprado} onChange={handleComprable} />
                                            <Form.Label htmlFor="puede_ser_comprado" className="cr" />
                                        </div>
                                    </Form.Group>

                                    <Form.Group as={Col} md="4">
                                        <Form.Label htmlFor="puede_ser_vendido">Puede ser vendido&nbsp;&nbsp;</Form.Label>
                                        <div className="switch d-inline m-r-10">
                                            <Form.Control type="checkbox" id="puede_ser_vendido" name="puede_ser_vendido" defaultChecked={puedeSerVendido} onChange={handleVedible} />
                                            <Form.Label htmlFor="puede_ser_vendido" className="cr" />
                                        </div>
                                    </Form.Group>

                                    {
                                        dataInicial.productoId <= 0 &&
                                        <Form.Group as={Col} md="4">
                                            <Form.Label htmlFor="es_perecedero">Es Perecedero&nbsp;&nbsp;</Form.Label>
                                            <div className="switch d-inline m-r-10">
                                                <Form.Control type="checkbox" id="es_perecedero" name="es_perecedero" defaultChecked={esPerecedero} onChange={handlePerecedero} />
                                                <Form.Label htmlFor="es_perecedero" className="cr" />
                                            </div>
                                        </Form.Group>
                                    }
                                    {
                                        dataInicial.productoId > 0 &&
                                        <Form.Group as={Col} md="6">
                                            <Form.Label htmlFor="estadoId">Estado</Form.Label>
                                            <SelectGroup
                                                name="estadoId"
                                                id="estadoId"
                                                value={values.estadoId}
                                                required
                                                errorMessage={errorMessage}
                                                onChange={handleOnChange}>
                                                <option value="">Seleccione un estado</option>
                                                <option value="1">Activo</option>
                                                <option value="2">Inactivo</option>
                                            </SelectGroup>
                                        </Form.Group>
                                    }

                                </Form.Row>
                                <hr></hr>
                                <h5>Asignación de Tipos de Presentación</h5>
                                {
                                    dataInicial.productoId <= 0 ? <>
                                        <strong style={{ color: 'red' }}>Por favor revise bien la información antes de registrar el producto, ya que solo sera posible modificar el precio de compra y venta</strong>
                                    </>
                                        : <>
                                            <strong style={{ color: 'red' }}>Para poder modificar esta información (cantidad, default compra y venta) es necesario que solicite, soporte ya que es muy sensible</strong>
                                        </>
                                }
                                <hr></hr>
                                <Form.Row>
                                    <TiposPresentacion
                                        addNuevo={addNuevo}
                                        setAddNuevo={setAddNuevo}
                                        idAuxRender={idAuxRender}
                                        editarItem={editarItem}
                                        setEditarItem={setEditarItem}
                                        productoId={dataInicial.productoId}
                                        listTipoPresentacion={listTipoPresentacion}
                                        listTipoPresentacionAsignados={listTipoPresentacionAsignados}
                                        setListTipoPresentacionAsignados={setListTipoPresentacionAsignados} />
                                </Form.Row>
                                <Form.Row>
                                    <div className="col-sm-4"></div>
                                    <div className="col-sm-2">
                                        <button type="button" onClick={() => { setAbrirModal(false) }} className="btn btn-warning"> Cancelar</button>
                                    </div>
                                    <div className="col-sm-2">
                                        <button type="submit" className="btn btn-success" disabled={addNuevo===true?true:editarItem===true?true:false}> {dataInicial.productoId > 0 ? 'Actualizar' : 'Registrar'}</button>
                                    </div>
                                </Form.Row>
                            </ValidationForm>
                        </Modal.Body>
                    </>
            }
        </Modal >
    )
}


const TiposPresentacion = ({addNuevo,setAddNuevo,editarItem,setEditarItem,idAuxRender,productoId = 0, listTipoPresentacion, listTipoPresentacionAsignados, setListTipoPresentacionAsignados }) => {
    const [existeTipoPresentacionDisponible, setExisteTipoPresentacionDisponible] = useState(true);
    const [enableDefaultCompra, setEnableDefaultCompra] = useState(true);
    const [enableDefaultVenta, setEnableDefaultVenta] = useState(true);

    const defaultData = {
        producto_tipo_presentacionId:0,
        tipo_presentacionId: '',
        nombre: '',
        unidades: '',
        precio_compra: '',
        precio_venta: '',
        default_compra: false,
        default_venta: false
    };
    const [listTipoPresentacionDisponibles, setListTipoPresentacionDisponibles] = useState(listTipoPresentacion);
    const handleAddFila = (itemForm) => {
        let existe = listTipoPresentacionAsignados.find(i => Number(i.tipo_presentacionId) === Number(itemForm.tipo_presentacionId));
        if (!existe) {
            let actuales = listTipoPresentacionAsignados || [];
            actuales.push(itemForm);
            setListTipoPresentacionAsignados(actuales);
        } else {
            let itemDetalle = listTipoPresentacionAsignados.map(item => {
                if (Number(item.tipo_presentacionId) === Number(itemForm.tipo_presentacionId)) {
                    return itemForm;
                } else {
                    return item;
                }
            });
            setEditarItem(false);
            setListTipoPresentacionAsignados(itemDetalle);

        }
    }

    const validarCheckBoxDefault = () => {
        let existeDefaultCompra = listTipoPresentacionAsignados.find(i => Boolean(i.default_compra) === true);
        let existeDefaultVenta = listTipoPresentacionAsignados.find(i => Boolean(i.default_venta) === true);
        if (existeDefaultCompra) {
            setEnableDefaultCompra(false);
        } else {
            setEnableDefaultCompra(true);
        }
        if (existeDefaultVenta) {
            setEnableDefaultVenta(false);
        } else {
            setEnableDefaultVenta(true);
        }
    }
    useEffect(() => {
        excluirTipodePresentacion();
        validarCheckBoxDefault();
        if (listTipoPresentacion.length === listTipoPresentacionAsignados.length) {
            setExisteTipoPresentacionDisponible(false);
        } else {
            setExisteTipoPresentacionDisponible(true);
        }
    }, [addNuevo, listTipoPresentacionAsignados]);

    const excluirTipodePresentacion = () => {
        let nuevaLista = [];
        listTipoPresentacion.map(item => {
            let existe = listTipoPresentacionAsignados.find(i => Number(i.tipo_presentacionId) === Number(item.tipo_presentacionId) && i.editar === false);
            if (!existe) {
                nuevaLista.push(item);
                console.log("llego", item)
            }
        });
        setListTipoPresentacionDisponibles(nuevaLista);
    }

    const eliminarFila = (id) => {
        let nuevos = listTipoPresentacionAsignados.filter(i => Number(i.tipo_presentacionId) !== Number(id))
        setListTipoPresentacionAsignados(nuevos);
    }

    const handleEditar = (tipo_presentacionId) => {
        let actuales = listTipoPresentacionAsignados;
        actuales = listTipoPresentacionAsignados.map(item => {
            if (item.tipo_presentacionId === tipo_presentacionId) {
                item.editar = true;
                if (Boolean(item.default_compra) === true) {
                    item.editDefaultCompra = true;
                } else {
                    item.editDefaultCompra = false;
                }
                if (Boolean(item.default_venta) === true) {
                    item.editDefaultVenta = true;
                } else {
                    item.editDefaultVenta = false;
                }
                return item;
            } else {
                return item;
            }
        });
        setEditarItem(true);
        setListTipoPresentacionAsignados(actuales);
        setAddNuevo(false);
    }
    return (
        <>
            <Table striped hover responsive bordered id="tableTipoPresentacionProducto">
                <thead>
                    <tr>

                        <th>No.</th>
                        <th>Nombre</th>
                        <th>Unidades</th>
                        <th>Precio de Compra</th>
                        <th>Precio de Venta</th>
                        <th>Default Compra</th>
                        <th>Default Venta</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {
                        listTipoPresentacionAsignados.map((item, index) => {
                            let { producto_tipo_presentacionId = 0, tipo_presentacionId, nombre, unidades, precio_compra, precio_venta, default_compra, default_venta, editar = false, editDefaultCompra = false, editDefaultVenta = false, TipoPresentacion = undefined } = item;
                            if (TipoPresentacion) {
                                nombre = TipoPresentacion.nombre;
                            }
                            let itemData = {
                                producto_tipo_presentacionId,
                                tipo_presentacionId,
                                nombre,
                                unidades,
                                precio_compra,
                                precio_venta,
                                default_compra,
                                default_venta,
                                editDefaultCompra,
                                editDefaultVenta,
                            }
                            return (
                                editar === false ?
                                    <tr key={`id_${tipo_presentacionId}`}>
                                        <td>{index + 1}</td>
                                        <td>{nombre}</td>
                                        <td>{Number.parseFloat(unidades).toFixed(2)}</td>
                                        <td>Q {Number.parseFloat(precio_compra).toFixed(2)}</td>
                                        <td>Q {Number.parseFloat(precio_venta).toFixed(2)}</td>
                                        <td style={{ textAlign: 'center' }}>
                                            <Form.Label htmlFor={`check_compraItem${tipo_presentacionId}`}>&nbsp;&nbsp;</Form.Label>
                                            <div className="switch d-inline m-r-10">
                                                <Form.Control type="checkbox" id={`check_compraItem${tipo_presentacionId}`} name={`check_compraItem${tipo_presentacionId}`} defaultChecked={default_compra} disabled />
                                                <Form.Label htmlFor={`check_compraItem${tipo_presentacionId}`} className="cr" title='Para poder modificarlo presione el boton editar' />
                                            </div>
                                        </td>
                                        <td style={{ textAlign: 'center' }}>
                                            <Form.Label htmlFor={`check_ventaItem${tipo_presentacionId}`}>&nbsp;&nbsp;</Form.Label>
                                            <div className="switch d-inline m-r-10">
                                                <Form.Control type="checkbox" id={`check_ventaItem${tipo_presentacionId}`} name={`check_ventaItem${tipo_presentacionId}`} defaultChecked={default_venta} disabled />
                                                <Form.Label htmlFor={`check_ventaItem${tipo_presentacionId}`} className="cr" title='Para poder modificarlo presione el boton editar' />
                                            </div>
                                        </td>

                                        <td>
                                            {
                                                !editarItem &&
                                                <button
                                                    type="button"
                                                    className="btn btn-info btn-sm mr-1"
                                                    onClick={() => { handleEditar(tipo_presentacionId) }}>
                                                    <i className="feather icon-edit" />
                                                </button>
                                            }
                                            {
                                                Number(producto_tipo_presentacionId) === 0 ?
                                                    <button
                                                        className="btn btn-danger btn-sm"
                                                        onClick={() => { eliminarFila(tipo_presentacionId) }}
                                                        type="button">
                                                        <i className="feather icon-trash-2" />
                                                    </button> : <></>
                                            }
                                        </td>
                                    </tr>
                                    : <FilaTipoPresentacion key={`tipo_presentacionDefault`}
                                        idAuxRender={idAuxRender}
                                        enableDefaultCompra={enableDefaultCompra}
                                        enableDefaultVenta={enableDefaultVenta}
                                        productoId={productoId}
                                        defaultData={itemData}
                                        listTipoPresentacion={listTipoPresentacionDisponibles}
                                        tipoPresentacionId={tipo_presentacionId}
                                        handleAddFila={handleAddFila}
                                        setAddNuevo={setAddNuevo}
                                        editarItem={editarItem}
                                    />
                            )
                        })
                    }
                    {
                        (listTipoPresentacionDisponibles.length > 0 && addNuevo === true) &&
                        <FilaTipoPresentacion
                            idAuxRender={idAuxRender}
                            enableDefaultCompra={enableDefaultCompra}
                            enableDefaultVenta={enableDefaultVenta}
                            productoId={productoId}
                            defaultData={defaultData}
                            listTipoPresentacion={listTipoPresentacionDisponibles}
                            handleAddFila={handleAddFila}
                            setAddNuevo={setAddNuevo}
                            editarItem={editarItem}
                        />
                    }
                </tbody>
            </Table>

            {
                (!addNuevo && !editarItem && existeTipoPresentacionDisponible) &&
                <Row className="align-items-center m-l-0">
                    <Col className="text-left">
                        {
                            <button type="button" className="btn btn-primary btn-sm" onClick={() => { setAddNuevo(true) }}><i className="feather icon-plus" />Nuevo</button>
                        }
                    </Col>
                    <Col />
                </Row>
            }
        </>
    )
}