import React, { useState, useEffect } from 'react'
import { Col, Form, Modal } from 'react-bootstrap';
import { ValidationForm, TextInput, SelectGroup } from 'react-bootstrap4-form-validation';
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import { useForm } from '../../hooks/useForm';
import Loading from '../base/Loading';
export const StockProductoUpSert = ({ dataInicial, abrirModal, setAbrirModal, GetListado, listSucursales }) => {
    const [values, handleOnChange] = useForm(dataInicial);
    const [loading, setLoading] = useState(false);
    const [editSucursal, setEditSucursal] = useState(false);
    const NuevoRegistro = async () => {
        if (Number(values.maximo) >=Number(values.minimo)) {
            let response = await callApi('stock', {
                method: 'POST',
                body: JSON.stringify(values)
            });

            if (response) {
                alert_exitoso("Se asignó el stock al producto exitosamente");
                GetListado();
                setAbrirModal(false);
            }
        } else {
            alert_warning("El stock máximo debe ser mayor al stock mínimo, por favor verifique")
        }
    }
    const ActualizarRegistro = async () => {
        if (Number(values.maximo) >=Number(values.minimo)) {
            let response = await callApi(`stock/${dataInicial.stockId}`, {
                method: 'PUT',
                body: JSON.stringify(values)
            });

            if (response) {
                alert_exitoso(response);
                GetListado();
            }
            setAbrirModal(false);
        } else {
            alert_warning("El stock máximo debe ser mayor al stock mínimo, por favor verifique")
        }
    }
    const handleOnSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        if (dataInicial.stockId > 0) {
            await ActualizarRegistro();
        } else {
            await NuevoRegistro();
        }
        setLoading(false);
    }
    const handleErrorSubmit = (e, formData, errorInputs) => {
        alert_warning("Por favor complete toda la información solicitada por el formulario");
    };

    useEffect(() => {
        if (dataInicial.stockId > 0) {
            setEditSucursal(true);
        }
    }, [])

    const errorMessage = "Campo obligatorio";
    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(false)}>
            {
                loading === true ?
                    <Loading />
                    : <>
                        <Modal.Header closeButton>
                            <Modal.Title as="h5">{dataInicial.stockId > 0 ? 'Actualizar Stock' : 'Asignar Stock'}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <ValidationForm onSubmit={handleOnSubmit} onErrorSubmit={handleErrorSubmit}>
                                <Form.Row>
                                    <Form.Group as={Col} md="12">
                                        <Form.Label htmlFor="sucursalId">Sucursal</Form.Label>
                                        <SelectGroup
                                            name="sucursalId"
                                            id="sucursalId"
                                            value={values.sucursalId}
                                            required
                                            errorMessage={errorMessage}
                                            onChange={handleOnChange}
                                            disabled={editSucursal}
                                        >
                                            <option value="">Seleccione una sucursal</option>
                                            {
                                                listSucursales.map(({ sucursalId, nombre }) => (
                                                    <option value={sucursalId} key={sucursalId}>{nombre}</option>
                                                ))
                                            }
                                        </SelectGroup>
                                    </Form.Group>

                                    <Form.Group as={Col} md="12">
                                        <Form.Label htmlFor="minimo">Disponibilidad mínima</Form.Label>
                                        <TextInput
                                            name="minimo"
                                            id="minimo"
                                            required
                                            value={values.minimo}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Disponibilidad mínima"
                                            autoComplete="off"
                                            type="number"
                                        />
                                    </Form.Group>
                                    <Form.Group as={Col} md="12">
                                        <Form.Label htmlFor="maximo">Disponibilidad máxima</Form.Label>
                                        <TextInput
                                            name="maximo"
                                            id="maximo"
                                            required
                                            value={values.maximo}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Disponibilidad máxima"
                                            autoComplete="off"
                                            type="number"
                                        />
                                    </Form.Group>
                                    <Form.Group as={Col} md="12">
                                        <Form.Label htmlFor="maximo">Disponible</Form.Label>
                                        <TextInput
                                            name="disponible"
                                            id="disponible"
                                            required
                                            value={values.disponible}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Cantidad disponible"
                                            autoComplete="off"
                                            type="number"
                                            readOnly={true}
                                        />
                                    </Form.Group>
                                    {
                                        dataInicial.stockId > 0 &&
                                        <Form.Group as={Col} md="12">
                                            <Form.Label htmlFor="estadoId">Estado</Form.Label>
                                            <SelectGroup
                                                name="estadoId"
                                                id="estadoId"
                                                value={values.estadoId}
                                                required
                                                errorMessage={errorMessage}
                                                onChange={handleOnChange}>
                                                <option value="">Seleccione un estado</option>
                                                <option value="1">Activo</option>
                                                <option value="2">Inactivo</option>
                                            </SelectGroup>
                                        </Form.Group>
                                    }
                                    <div className="col-sm-3"></div>
                                    <div className="col-sm-3">
                                        <button type="button" onClick={() => { setAbrirModal(false) }} className="btn btn-warning"> Cancelar</button>
                                    </div>
                                    <div className="col-sm-3">
                                        <button type="submit" className="btn btn-success"> {dataInicial.stockId > 0 ? 'Actualizar' : 'Asignar'}</button>
                                    </div>
                                </Form.Row>
                            </ValidationForm>
                        </Modal.Body>
                    </>
            }
        </Modal>
    )
}
